import React, { Component } from 'react';
import { Jumbotron } from 'reactstrap';
import { fadeInUp, merge, bounceInRight, pulse } from 'react-animations';
import styled, { keyframes } from 'styled-components';

const myAnim = merge(pulse, bounceInRight);
const ImgAnim = styled.div`animation: 2s ${keyframes`${myAnim}`} 1`;
const TextAnim = styled.div`animation: 2s ${keyframes`${fadeInUp}`} 1`;

class About extends Component{
    render(){
        return(
            <React.Fragment>
                <Jumbotron>
                    <div className="container" id="about">
                        <div className="row">
                            <div className="col-md-8">
                                <TextAnim>
                                    <p>Hi, my name is</p>
                                    <p id="myname"><strong>Sowrya Gali</strong></p> 
                                    <p>I'm currently pursuing B.Tech in Computer Science and Engineering at IIT Hyderabad. I'm interested in areas of Deep learning, Algorithms and Web/App development. I'm from Tenali, Andhra Pradesh. I love solving math problems and designing solutions using Machine learning.</p>
                                </TextAnim>
                            </div>
                            <div className="col-md-4" id="myphoto">
                                <ImgAnim>
                                    <img src="img/sowrya.jpg" className="img-fluid" alt="Sowrya Gali" />
                                </ImgAnim>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
            </React.Fragment>
        );
    }
}

export default About;