import React, { Component } from 'react';

const skills = [
    {
        property: "Languages", 
        set: ["C","C++","Python","Javascript","Java","MATLAB","Haskell","Prolog"]
    },
    {
        property:"Frameworks", 
        set: ["TensorFlow","PyTorch"]
    },
    {
        property: "Webdev", 
        set: ["HTML5","CSS3","Javascript","Bootstrap","jQuery","ReactJS"]
    },
    {
        property: "Appdev", 
        set: ["React Native"]
    },
    {
        property:"Devtools",
        set: ["Git","Linux","Latex"]
    },
    {
        property:"Hardware",
        set: ["Arduino"]
    }
];

const procesSet = (set) => {
    var i = 0;
    return(
        set.map((item) => {
            return(
                <span key={i++} className="fa" aria-hidden="true"><img src={"icons/" + item + ".svg"} className="img-fluid icon-resize" alt={item + "icon"} />{item} &nbsp;</span>
            );
        })
    );
}

const processProp = (skills) => {
    var i = 0;
    return(
        skills.map((skill) => {
            return(
                <div key={i++} className="row skill-row">
                    <div className="col-md-3">
                            <h3>{skill.property}</h3>
                    </div>
                    <div className="col-md-9">
                            {procesSet(skill.set)}
                    </div>
                    <br /><br /><br />
                </div>
            );
        })
    );
}

class Skills extends Component{
    render(){
        return(
            <div className= "container" id = "skills">
                <h1>Skills</h1>
                
                    {processProp(skills)}
                
            </div>
        );
    }
}

export default Skills;