import React, { useState } from 'react';
import { Carousel, CarouselItem, CarouselControl, CarouselIndicators, Card, CardBody, CardImg, CardTitle, CardSubtitle, CardText } from 'reactstrap';

const items = [
    {
      src: 'img/portfolio.jpg',
      altText: 'portfolio',
      caption: 'La Cartera',
      description: "The website you are currently looking at, showcasing my developer skills. It is developed using bootstrap framework.",
      tools: ["HTML5", "CSS3", "Bootstrap", "Javascript", "jQuery", "ReactJS"],
      url: "https://github.com/sowgali/sowgali.github.io"
    },
    {
      src: 'img/nn.jpg',
      altText: 'Neural Network',
      caption: 'NeuraConnect',
      description: "Extract embeddings into a common vector space, easing image-captioning, searching images based on description.",
      tools: ["Python", "TensorFlow"],
      url: "#"
    },
    {
      src: 'img/compiler.png',
      altText: 'Compiler',
      caption: 'The Compiler',
      description: "Building a compiler for COOL language using ANTLR, as part of Compilers course at IITH.",
      tools: ["Java", "antlr"],
      url: "#"
    },
    {
      src: 'img/twitter.jpg',
      altText: 'Twitter-Floods',
      caption: 'Flood-Tweets Project',
      description: "A classifier to filter tweets relevant to natural disaster relief and further identify contact information of emergency personnel.",
      tools: ["Python", "TensorFlow"],
      url: "https://github.com/sowgali/Flood_Tweets"
    }
  ];

const Projects = () => {
    
    const [activeIndex, setActiveIndex] = useState(0);
    const [animating, setAnimating] = useState(false);
    const next = () => {
      if (animating) return;
      const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
      setActiveIndex(nextIndex);
    }
  
    const previous = () => {
      if (animating) return;
      const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
      setActiveIndex(nextIndex);
    }
  
    const goToIndex = (newIndex) => {
      if (animating) return;
      setActiveIndex(newIndex);
    }

    const fetchIcons = (tools) => {
      var i = 0;
        return(
            tools.map((tool) => {
                return(
                    <li key={i++}><span className="fa icon-resize" aria-hidden="true"><img src={"icons/" + tool + ".svg"} className="img-fluid icon-resize" alt = {tool + " icon"} /></span></li>
                );
            })
        );
    }
  
    const slides = items.map((item) => {
      return (
        <CarouselItem
          onExiting={() => setAnimating(true)}
          onExited={() => setAnimating(false)}
          key={item.src}
        >
            <Card>
                <CardImg top className="myCardImg" src={item.src} alt={item.altText} />
                <CardBody className="myCardBody">
                    <CardTitle><strong>{item.caption}</strong></CardTitle>
                    <CardSubtitle>
                        <ul className="list-group list-group-horizontal list-unstyled">
                            {fetchIcons(item.tools)}
                        </ul>
                    </CardSubtitle>
                    <CardText>{item.description}</CardText>
                    <a role="button" color="secondary" className="btn btn-secondary offset-10" rel="noopener noreferrer" target="_blank" href={item.url}> Code </a>
                </CardBody>
            </Card>
        </CarouselItem>
      );
    });
  
    return (
        <div className="container" id = "projects" >
            <h1>Projects</h1>
            <Carousel
                activeIndex={activeIndex}
                next={next}
                previous={previous}
                
            >
                <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
                {slides}
                <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
                <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
            </Carousel>
        </div>
    );
  }

export default Projects;