import React, { Component } from 'react';
import { Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem } from 'reactstrap';
import { bounceInDown } from 'react-animations';
import styled, { keyframes } from 'styled-components';

const HeaderAnim = styled.div`animation: 2s ${keyframes`${bounceInDown}`} 1`;

class Header extends Component{
    constructor(props){
        super(props);
        this.state = {
            isNavOpen : false,
            prevScrollpos: window.pageYOffset,
            visible: true
        };
        this.toggleNav = this.toggleNav.bind(this);
        this.handleScroll = this.handleScroll.bind(this);
    }

    handleScroll = () => {
        const prevScrollpos = this.state.prevScrollpos;
        
        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;
        
        this.setState({
            prevScrollpos: currentScrollPos,
            visible: visible
        });
    };

    componentDidMount() {
        window.addEventListener("scroll", this.handleScroll);
      }
      
    componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
    }

    toggleNav() {
        this.setState({
          isNavOpen: !this.state.isNavOpen
        });
    }

    render(){
        return(
            <React.Fragment>
                <HeaderAnim>
                    <Navbar dark expand="md" className={"myNav" + (this.state.visible ? "" : " navbar--hidden")}>
                        <div className="container">
                            <NavbarToggler onClick={this.toggleNav} />
                            <Collapse isOpen={this.state.isNavOpen} navbar>
                                <NavbarBrand to="/">
                                    <img src="img/logo.png" className="mr-auto img-fluid" id ="logo" alt="SGali logo" />
                                </NavbarBrand>
                                <Nav className="mx-auto" navbar>
                                    <NavItem>
                                        <a className = "nav-link" href="#about" onClick={this.toggleNav}>About</a>
                                    </NavItem>
                                    <NavItem>
                                        <a className = "nav-link" href="#edu">Education</a>
                                    </NavItem>
                                    <NavItem>
                                        <a className = "nav-link" href="#projects" onClick={this.toggleNav}>Projects</a>
                                    </NavItem>
                                    <NavItem>
                                        <a className = "nav-link" href="#work">Work</a>
                                    </NavItem>
                                    <NavItem>
                                        <a className = "nav-link" href="#acad">Acad</a>
                                    </NavItem>
                                    <NavItem>
                                        <a className = "nav-link" href="#skills">Skills</a>
                                    </NavItem>
                                    <NavItem>
                                        <a className = "nav-link" href="#contact">Contact Me</a>
                                    </NavItem>
                                </Nav>
                            </Collapse>
                            <Nav className="ml-auto" navbar>
                                <NavItem>
                                    <a role="button" color="secondary" className="btn btn-secondary ml-auto" rel="noopener noreferrer" target="_blank" href="https://drive.google.com/file/d/1StS6P46AeBeeUuenykZGXKZ3SdgF-Ch8/view?usp=sharing" id="resumeButton"> Resume </a>
                                </NavItem>
                            </Nav>
                        </div>
                    </Navbar>
                </HeaderAnim>
            </React.Fragment>
        );
    }
}

export default Header;